body{
  background-color: #03012f;
  color: white;
  margin: 20px;
  font: 32px "Century Gothic", Futura, sans-serif;
}
.game{
  
    display: flex;
    flex-direction: column;
    align-items: center;
  }
.Board-row{
  display: flex;
  flex-flow: row nowrap;
}  
.square{
  background-color: cornflowerblue;
  color: rgb(21, 1, 82);
  border: 1px solid #999;
  padding: 0;
  font-size: 84px;
  width: 100px;
  height: 100px;
  text-align: center;
}

.square:focus{
  outline: none;
  background-color: rgb(194, 228, 113);
}

.status{
  margin: 20px;
  font-size: 24px;
}